import React from "react";
import ContactUsView from "./ContactUsView";
import ContactUsModel from "./ContactUsModel";


const ContactUsController = (props) => {
    const { onSubmitForm} = ContactUsModel();

    const onSubmitFormController = async(arg)=>{
        return onSubmitForm(arg)

    }
    const view = () => (<React.Fragment> 
        <ContactUsView onSubmitForm={onSubmitFormController} />
    </React.Fragment>

    )
    return {view}
}

export default ContactUsController


