import { connect } from 'react-redux'
import ContactUs from 'Shared/layouts/ContactUs'
import { getLocationPhoneNumber, isOlivReachable } from 'Shared/selectors/geolocation'
import { isMobile } from 'Shared/helpers/mobile'

const mapStateToProps = state => ({
  phoneNumber: getLocationPhoneNumber(state),
  isOlivReachable: isOlivReachable(state),
  isMobile: isMobile(state)
})

export default connect(mapStateToProps)(ContactUs)



