import React from 'react'
import useApiContactUsForm from 'Shared/services/api/useApiContactUsForm'

const ContactUsModel = () => {
  const {apiContactUsForm} = useApiContactUsForm()

  const onSubmitForm = async (arg) => {
    return apiContactUsForm(arg)
  }

  return {onSubmitForm}
}

export default ContactUsModel



