import InstaIcon from 'Shared/assets/images/icons/insta-icon.svg'
import TwitterIcon from 'Shared/assets/images/icons/twitter-icon.svg'
import FacebookIcon from 'Shared/assets/images/icons/facebook-icon.svg'
import LinkedInIcon from 'Shared/assets/images/icons/linkedIn-icon.svg'

export const Icons = [
  {
    svg: InstaIcon,
    link: 'https://www.instagram.com/oliv/',
    key: 'Oliv\'s Instagram'
  },
  {
    svg: TwitterIcon,
    link: 'https://twitter.com/OlivHQ',
    key: 'Oliv\'s Twitter'
  },
  {
    svg: FacebookIcon,
    link: 'https://www.facebook.com/OlivHQ/',
    key: 'Oliv\'s Facebook'
  },
  {
    svg: LinkedInIcon,
    link: 'https://www.linkedin.com/company/olivhq',
    key: 'Oliv\'s LinkedIn'
  }
]



