import React from 'react'
import { postRequest} from './commonApi'
import { endPoints } from './endPoints'

const useApiContactUsForm = () => {
  const apiContactUsForm = async (data) => {
    const url = endPoints.contactForm

    try {
      let res = await postRequest(url, data)

      if (res.success) {
        return res
      }
    } catch (err) {
      console.log('ERROR', err)
    }
  }

  return { apiContactUsForm }
}

export default useApiContactUsForm



