import React, { useState } from 'react'
import withTranslation from 'Shared/hoc/withTranslations'
import './ContactUsView.scss'
import { isValidValue } from 'Shared/helpers/validator'
import earthIcon from 'Shared/assets/images/logos/earth-icon.svg'
function ContactUsView(props) {
    const { onSubmitForm,t } = props;
    const [formError, setFormError] = useState({ name: "", email: "", phone: "", description: ""})
    const [isValidForm, setIsValidForm] = useState(false)
    const [inputs, setInputs] = useState({})
    const [formResponse, setFormResponse] = useState(false)
    const [isDisable,setIsDisable] = useState(false)
    const ERROR = {
        name: formError.name,
        email: formError.email,
        phone: formError.phone,
        description: formError.description
    }
    const isValidateUserFormHandler = () => {
        const noErrors = Object.values(formError).every((v) => v === true);
        if (noErrors) { setIsValidForm(true); return true }

        setIsValidForm(false)
        return false
    }
    const handleInputChange = (e) => {
        const { value, name, type } = e.target
        setInputs(prev => ({ ...prev, [name]: value }))
        setFormError((prev) => ({ ...prev, [name]: !!isValidValue(value, type, true) }))
        isValidateUserFormHandler()
    }
    const handleForm = () => {

        document.getElementById('contact-form').reset();

        setFormError({ name: "", email: "", phone: "", description: "" })

        setIsValidForm(false)

        setInputs({})

        setFormResponse(true);

        setTimeout(()=>{
            setFormResponse(false);
        },3000)

    }
    const handleSubmit = async () => {
        if (!isValidForm) return
        setIsDisable(true)
        const resp = await onSubmitForm({ name: inputs.name, email: inputs.email, phone: inputs.phone, description: inputs.description })
        try{
            if (resp.success) {
                handleForm();
            }
        }catch(error){
            console.log("ERROR",error)
        }finally{
            setIsDisable(false)
        }

    }
    return (
        <React.Fragment>
            <div className='column col-6 col-sm-12'>
                <div className='contact-us-form'>
                    <form id='contact-form' autoComplete='off'>
                        <h1>{t('contactUsPage.form.heading', 'Contact us')}</h1>
                        <h3>{t('contactUsPage.form.title', 'Report bugs, request features, and contact our team for account and billing help.')}</h3>
                        <div className='form-group' data-valid={ERROR['name']} >
                            <label>{t('contactUsPage.form.full_name_label', 'Full Name')}</label>
                            <input name='name' type='text' placeholder={t('contactUsPage.form.full_name_placeholder', 'Enter your full name')}
                                onKeyUp={(e) => handleInputChange(e)}
                                onChange={(e) => handleInputChange(e)}
                                required
                                autoComplete='none'
                            />
                        </div>

                        <div className='form-group' data-valid={ERROR['email']}>
                            <label>{t('contactUsPage.form.email_label', 'Email')}</label>
                            <input name='email' type='email' placeholder={t('contactUsPage.form.email_placeholder', 'Enter your email')}
                                onKeyUp={(e) => handleInputChange(e)}
                                onChange={(e) => handleInputChange(e)}
                                required
                                autoComplete='none'
                            />
                        </div>

                        <div className='form-group' data-valid={ERROR['phone']}>
                            <label>{t('contactUsPage.form.phone_label', 'Phone Number')}</label>
                            <input name='phone' type='tel' placeholder={t('+00 000 000 0000')}
                                onKeyUp={(e) => handleInputChange(e)}
                                onChange={(e) => handleInputChange(e)}
                                required
                                autoComplete='none'
                                onKeyPress={(event) => {
                                    if (!/[0-9]/.test(event.key)) {
                                        event.preventDefault()
                                    }
                                }}
                            />
                        </div>
                        <div className='form-group' data-valid={ERROR['description']}>
                            <label>{t('contactUsPage.form.description_label', 'DESCRIPTION')}</label>
                            <textarea name='description' placeholder={t('contactUsPage.form.description_placeholder', "Talk about the problem you're facing")}
                                onKeyUp={(e) => handleInputChange(e)}
                                onChange={(e) => handleInputChange(e)}
                                required
                                autoComplete='none'
                            />

                        </div>

                    </form>
                    <div className='btn'> 
                    <button className={`form-btn ${!isValidForm && 'disabled_btn'} col-sm-12`}
                        onClick={() => {
                            handleSubmit()
                        }}
                        disabled={!isValidForm || isDisable}
                    >
                        {t('contactUsPage.form.submit_btn_text', 'Submit Request')}
                    </button>
                    {formResponse ? 
                    <span className='form-response'>
                        Submit Successfully
                    </span>
                    :
                    ''}
                    </div>
                </div>
            </div>

        </React.Fragment>
    )
}

export default withTranslation(ContactUsView)



