import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import { withRouter } from 'react-router-dom'

import withTranslation from 'Shared/hoc/withTranslations'
import SocialIcons from 'Shared/layouts/ContactUs/components/SocialLogos'
import Container from 'Shared/components/Container'
import { SUPPORT_EMAIL } from 'Shared/constants/Global'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import { formatPhoneNumber } from 'Shared/helpers/formats'
import { cond, propEq, pipe, propOr, T } from 'ramda'
import contact from 'Shared/assets/images/vectors/contact-icon-call.svg'
import email from 'Shared/assets/images/vectors/contact-icon-email.svg'
import location from 'Shared/assets/images/vectors/contact-icon-location.svg'
import './ContactUs.scss'
import contactImage from 'Shared/assets/images/brand/contact-us-form.svg'
import { contactPage } from 'Shared/constants/MetaTags'
import ContactUsController from './ContactUsController'

const {view: ContactUsView} = ContactUsController();
const hasPhoneNumber = pipe(
  propOr('', 'phoneNumber'),
  isNotNilOrEmpty
)

const emptyInfo = () => <div />

class ContactUs extends Component {
  state = {
    ready: false
  }

  componentWillReceiveProps ({ isScriptLoaded, isScriptLoadSucceed }) {
    if (isScriptLoaded && isScriptLoadSucceed) {
      this.setState({ ready: true })
    }
  }

  getPhoneNumber = () => (
    <div className='contact-container--company__phone'>
      <img
        className='contact-container--company__icon'
        src={contact}
        alt='contact icon'
      />
      {formatPhoneNumber(this.props.phoneNumber)}
    </div>
  )

  getEmail = () => (
    <div className='contact-container--company__email'>
      <img
        className='contact-container--company__icon'
        src={email}
        alt='contact icon'
      />
      {SUPPORT_EMAIL}
    </div>
  )

  getContact = () => cond([
    [propEq('isOlivReachable', false), emptyInfo],
    [hasPhoneNumber, this.getPhoneNumber],
    [T, this.getEmail]
  ])(this.props)

  render() {
    const { t, isMobile } = (this.props)
    return (
      <section className='contact-us__container parsing--hidden'>
        <Helmet>
          <title>{contactPage.title}</title>
          <meta name='description' content={contactPage.description} />
          <meta property='og:title' content={contactPage.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={contactPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={contactPage.url} />
          <meta property='og:description' content={contactPage.description} />
        </Helmet>
        <Container>
          <div className='contact-container'>
            <ContactUsView />
            <div className='about-container column col-6 col-sm-12'>
              {!isMobile ?
              <img src={contactImage}/>
                : ''}
              <div className='column col-12 col-sm-12 contact-container--about'>
                <div className='contact-container--about__heading'> {t('contactPage.aboutUs')} </div>
                <div className='contact-container--about__more'>
                  <p>{t('contactPage.aboutUsMore_1')} </p>
                  <p>{t('contactPage.aboutUsMore_2')} </p>
                </div>
                  <SocialIcons/>
              </div>
            </div>
          </div>
        </Container>
      </section>
    )
  }
}

export default withRouter(withTranslation(ContactUs))



