import React from 'react'
import { Icons } from '../enums/SocialIcons'
import './SocialIcon.scss'

const SocialIcons = () => {
  const getIcons = () => {
    return Icons
      .map((icon, index) => (
        <span key={icon.key}><a href={icon.link}><img src={icon.svg} /></a></span>)
      )
  }

  return <div className='social-icons'>{getIcons()}</div>
}

export default SocialIcons



